.c-form {
  margin-bottom: 2rem;
  .c-form__select {
    position: relative;
    pointer-events: none ;
    &:after {
      font-family: 'Material Icons';
      position: absolute;
      right: 1rem;
      top: 50%;
      transform: translateY(-50%);
      content: "keyboard_arrow_down";
    }
    select {
      pointer-events: auto;
    }
  }
}
