@font-face {
  font-family: 'vidra-social';
  src:  url('/public/assets/fonts/vidra-social.eot?z9wurc');
  src:  url('/public/assets/fonts/vidra-social.eot?z9wurc#iefix') format('embedded-opentype'),
    url('/public/assets/fonts/vidra-social.ttf?z9wurc') format('truetype'),
    url('/public/assets/fonts/vidra-social.woff?z9wurc') format('woff'),
    url('/public/assets/fonts/vidra-social.svg?z9wurc#vidra-social') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="vidra-icon-"], [class*=" vidra-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'vidra-social' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: sub;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

