.c-product {
  .c-product__gallery {
    margin-bottom: $spacer * 2;
    @include media-breakpoint-up(md) {
      margin-bottom: 0;
    }
  }
  .c-product__title {
    font-size: 2rem;
  }
  .c-product__subtitle {
    font-size: 1.125rem;
    color: $gray-400;
  }
  .c-product__price {
    font-size: 1.5rem;
    color: $theme-2;
    font-weight: 600;
    margin-bottom: map-get($spacers, 3) * 2;
    span {
      margin-left: map-get($spacers, 3);
      color: $gray-400;
      font-weight: 500;
      font-size: 0.875rem;
      cursor: pointer;
      text-decoration: underline;
      transition: color .3s ease-in-out;
      &:hover {
        color: darken($gray-400,20%);
      }
    }
  }
  .c-product__info {
    font-size: $font-size-sm;
    .c-product__info-title {
      font-size: 1rem;
    }
  }
}
