/*	Hide the original text of the button. Then the loading or finished
	text will be shown in the :after element above it. */

.progress-button.in-progress,
.progress-button.finished{
	color:transparent !important;
}

.progress-button.in-progress:before,
.progress-button.finished:before{
	position: absolute;
	z-index: 5;
	width: 100%;
	height: 100%;
	text-align: center;
	top: 0;
	padding-top: inherit;
	color: #ffffff !important;
	left: 0;
}

/*	If the .in-progress class is set on the button, show the
	contents of the data-loading attribute on the butotn */

.progress-button.in-progress:before{
	content:attr(data-loading);
}

/* The same goes for the .finished class */

.progress-button.finished:before{
	content:attr(data-finished);
}

/* The colorful bar that grows depending on the progress */

.progress-button .tz-bar{
	background-color: darken($primary,20%);
	height:3px;
	bottom:0;
	left:0;
	width:0;
	position:absolute;
	z-index:4;

	border-radius:0 0 2px 2px;

	-webkit-transition: width 0.5s, height 0.5s;
	-moz-transition: width 0.5s, height 0.5s;
	transition: width 0.5s, height 0.5s;
}

/* The bar can be either horizontal, or vertical */

.progress-button .tz-bar.background-horizontal{
	height:100%;
	border-radius: 30px!important;
}

.progress-button .tz-bar.background-vertical{
	height:0;
	top:0;
	width:100%;
	border-radius:2px;
}


/*----------------------------
	Color themes
-----------------------------*/


.progress-button.red{
	background-color: $danger;
	background-image:-webkit-linear-gradient(to bottom, #e6537d, #df5179);
	background-image:-moz-linear-gradient(to bottom, #e6537d, #df5179);
	background-image:linear-gradient(to bottom, #e6537d, #df5179);
}

.progress-button.red .tz-bar{
	background-color: darken($danger,20%);
}


.progress-button.green{
	background-color: $success;
	background-image:-webkit-linear-gradient(to bottom, #64c896, #5fbd8e);
	background-image:-moz-linear-gradient(to bottom, #64c896, #5fbd8e);
	background-image:linear-gradient(to bottom, #64c896, #5fbd8e);
}

.progress-button.green .tz-bar{
	background-color:#9e81d6;
}
