.c-carousel {
   .c-carousel__caption {
     background-color: rgba(0,0,0,.3);
     right: 0;
     left: 0;
     top: 0;
     bottom: 0;
     .c-carousel__caption--title,
     .c-carousel__caption--text {
       font-size: 2rem;
     }     
     .c-carousel__caption--container {
       display: table;
       width: 100%;
       height: 100%;
     }
     .c-carousel__caption--content {
       display: table-cell;
       width: 100%;
       vertical-align: middle;
     }
   }
}
