.e-btn-circle {
  position: relative;
  width: $font-size-base * 2.5;
  height: $font-size-base * 2.5;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: .5rem;
  padding-left: .5rem;
  font-size: $font-size-base * 0.875;
  line-height: 1.428571429;
  border-radius: $font-size-base * 1.25;
  &.btn-lg {
    width: $font-size-base * 3;
    height: $font-size-base * 3;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: $font-size-lg;
    line-height: 1.33;
    border-radius: $font-size-lg;
  }
  &.btn-sm {
    width: 2rem;
    height: 2rem;
    padding-top: .2rem;
    padding-bottom: .2rem;
    font-size: $font-size-sm;
    line-height: 1.33;
    border-radius: 2rem;
  }
}
