/* Custom styles */
/* FORMS
========================================================================== */
.form-check {
  padding-top: $form-check-input-padding-y;
  padding-bottom: $form-check-input-padding-y;
  .form-check-label {
    font-size: $font-size-sm;
  }
}
.form-group:nth-last-child(1) {
  margin-bottom: 2rem !important;
}
label {
  font-size: $font-size-sm;
  font-weight: 600;
}
/* PAGINATION
========================================================================== */
.page-link {
  margin-left: .5rem !important;
  padding: .4rem .825rem !important;
  i {
    font-size: inherit !important;
    vertical-align: middle !important;
    color: darken($pagination-border-color,10%) !important;
  }
}

/* BUTTONS
========================================================================== */
.btn-gray--light {
  background-color: $gray-200;
  color: $gray-400;
  transition: all .3s ease-in-out;
  &:hover {
    background-color: $gray-400;
    color: $gray-200;
  }
}
.btn-xlg {
  @include button-size($btn-padding-y-xlg, $btn-padding-x-xlg, $font-size-base, $btn-line-height-xlg, $btn-border-radius-xlg);
  text-transform: uppercase;
}
/* GO TOP & LOADER
========================================================================== */
  #go-top {
  	position: fixed;
  	float: right;
  	left: 44px;
  	bottom: 44px;
  	z-index: 9999;
  	display: none;
  	cursor: pointer;
  	font-size: $font-size-base;
  	opacity: 0.7;
    border: 0;
    background-color: transparent;
    color: $primary;
    padding: 12px 16px;
  	i {
  		text-align: center;
  	}
  	&:hover {
  		opacity: 1;
      transform: translateY(-5px);
      transform-style: preserve-3d;
  	}
  }

  .loader {
  	position: fixed;
  	left: 0px;
  	top: 0px;
  	width: 100%;
  	height: 100%;
  	z-index: 120000;
  	background: #fff;
    .loader__img {
      position: relative;
      width: 100%;
      height: 100%;
      background: transparent url(/public/assets/images/xloading.gif) 50% 50% no-repeat;
    }
  }
