.c-category{}

.c-filters {
  .c-filters-title {
    font-size: 1.125rem;
    margin-bottom: .5rem;
    position: relative;
  }
  .c-filters-title--collapse {
    padding: $spacer;
    background-color: $gray-200;
    &:after {
      position: absolute;
      right: $spacer;
      font-family: "Material Icons";
      content: "keyboard_arrow_down";
    }
    @include media-breakpoint-up(md) {
      padding: 0;
      background-color: transparent;
      &:after {
        content: "";
      }
    }
  }
  .c-filters-subtitle {
    margin-top: 1rem;
    color: $theme-2;
    font-size: 1rem;
    font-weight: 500;
  }
  .c-filters-container {
    background-color: $gray-100;
    padding-left: $spacer;
    padding-right: $spacer;
    @include media-breakpoint-up(md) {
      background-color: transparent;
      padding: 0;
    }
  }
}
