.c-cards {
  .c-card__item {
    text-align: center;
    margin-bottom: 48px;
    border-radius: 0;
    border: 0;
    position: relative;
  }
  .c-card__img {
    display: block;
    margin-bottom: 24px;
  }
  .c-card__badge {
    position: absolute;
    top: 16px;
    text-transform: uppercase;
    &--sx {
      left: 16px;
    }
    &--dx {
      right: 16px;
    }
  }
  .c-card__body {

  }
  .c-card__title {
    color: $theme-1;
    margin-bottom: 8px;
  }
  .c-card__text {

  }
  .c-card__price {
    display: inline-block;
    margin: 0 auto;
    font-weight: 900;
    color: $theme-2;
    position: relative;
    &::after {
      position: absolute;
      content: "€";
    }
  }
  .c-card__btn {
    display: none;
  }
}
