/* Compatibility styles for frameworks like bootstrap, foundation e.t.c */
.xzoom-source img, .xzoom-preview img, .xzoom-lens img {
  display: block;
  max-width: none;
  max-height: none;
  transition: none;

}
/* --------------- */

/* xZoom Styles below */
.xzoom-container {
  display: inline-block;
    border-radius: $border-radius-lg;
}

.xzoom-thumbs {
  text-align: center;
  margin-bottom: 10px;
}

.xzoom {
  border-radius: $border-radius-lg;
}
.xzoom2, .xzoom3, .xzoom4, .xzoom5 {

}

/* Thumbs */
.xzoom-gallery, .xzoom-gallery2, .xzoom-gallery3, .xzoom-gallery4, .xzoom-gallery5 {
  border: $border-width solid $gray-400;;
  margin-bottom: 1rem;
  margin-right: .5rem;
  border-radius: $border-radius;
}

.xzoom-source, .xzoom-hidden {
  display: block;
  position: static;
  float: none;
  clear: both;
}

/* Everything out of border is hidden */
.xzoom-hidden {
  overflow: hidden;
}

/* Preview */
.xzoom-preview {
  border: 0;
  background: $gray-200;
}

/* Lens */
.xzoom-lens {
  border: $border-width solid $gray-400;
  box-shadow: -0px -0px 10px rgba($primary,0.5);
  cursor: crosshair;
}

/* Loading */
.xzoom-loading {
  background-position: center center;
  background-repeat: no-repeat;
  border-radius: 100%;
  opacity: .7;
  background: url(/public/assets/images/xloading.gif);
  width: 48px;
  height: 48px;
}

/* Additional class that applied to thumb when it is active */
.xactive {
  border: $border-width solid $primary;
}

/* Caption */
.xzoom-caption {
  position: absolute;
  bottom: -43px;
  left: 0;
  background: $primary;
  width: 100%;
  text-align: left;
}

.xzoom-caption span {
  color: #fff;
  display: block;
  font-size: 0.75em;
  font-weight: bold;
  padding: 10px;
}
