.c-header {
  transition: all 0.1s ease-in-out;
  .nav, .collapse, .dropup, .dropdown {
    position: static;
  }
  .container {
    position: relative;
  }
  .nav-item {
    text-align: center;
    //position: relative;
    &.show {
      &::after {
        content: url(/public/assets/images/arrow-up.svg);
        position: absolute;
        width: 18px;
        line-height: 0.9;
        bottom: -4px;
        z-index: 1001;
        transform: translateX(-50%);
        color: $light;
        background-color: #fff;
      }
    }
  }
  .nav-link {

  }
  .dropdown {
    .nav-link {}
  }
  .dropdown-menu {
    left: 0;
    right: 0;
    margin: 0;
    padding-top: 40px;
    padding-bottom: 40px;
    border-width: 1px;
    border-bottom:0;
    border-right: 0;
    border-left: 0;
    border-radius: 0;
    border-color: $light;
    box-shadow: 0 3px 6px 0 rgba(0,0,0,0.1);
  }
  &__section {
    font-size: 14px;
    line-height: 1.4;
    color: $theme-1;
    &-title {
      color: $primary;
      font-weight: bold;
      font-size: 1.4rem;
      border-bottom: 2px solid rgba($primary, 0.1);
      padding: 1rem 0;
      margin:0;
    }
    &-description {
      font-size: 0.9rem;
      border-bottom: 2px solid rgba($primary, 0.1);
      padding: 1rem 0;
    }
    &-list {
      color: $primary;
      font-size: 1.2rem;
      padding: 1rem 0;
      li {
        margin: 0.5rem 0;
      }
    }
    a {
      position: relative;
      transition: all 0.1s ease-in-out;
    }
  }
  @media only screen and (max-width: 1023px) {
    .nav-item {
      .nav-link {
        border-bottom: 2px solid #efeff1;        
      }
      &:last-child {
        .nav-link {
          border:0;
        }
      }
    }

  }
  /* The sticky class is added to the navbar with JS when it reaches its scroll position */
  &.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: $zindex-sticky;
    box-shadow: 0 3px 6px 0 rgba(0,0,0,0.1);
  }
  .navbar-brand {
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-align: left;
    @media only screen and (max-width: 1023px) {
      text-align: center;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
    }
  }
  @media only screen and (max-width: 1023px) {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}

.c-header__buttons {
  .nav-link {
    padding-right: .5rem !important;
    padding-left: .5rem !important;
  }
  .badge {
    position: absolute;
    top: -4px;
    right: -4px;

  }
  i {
    font-size: $font-size-base;
  }
  @media only screen and (max-width: 1023px) {
    position: absolute;
    right: 1rem;
    top: 1rem;
    width: auto !important;
    .nav-link {
      border: 0 !important;
      padding: 0 !important;
    }
  }
}
