.c-footer {
  text-align: center;
  padding: 80px 0;
  .c-footer__list {
    margin-bottom: 32px;
    font-size: 0.9rem;
    color: $theme-1;
    li {
      padding: 0 8px 0 0;
      border-right: 1px solid lighten($theme-1,40%);
      &:last-child {
        border: 0;
      }
    }
  }
  .c-footer__logo {
    margin-bottom: 16px;
  }
  .c-footer__text {
    font-size: 0.8rem;
    color: $gray-400;
    text-transform: uppercase;
  }
}
